@mixin px2-grid__col(){
    margin: 0 0.5rem 0 0.5rem;
    box-sizing: border-box;
}
.px2-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0rem;
    margin: 0 -0.5rem 0 -0.5rem;
    box-sizing: border-box;

    &__col-1of1 {
        @include px2-grid__col();
        width: calc( 100% - 1rem );
    }
    &__col-1of2 {
        @include px2-grid__col();
        width: calc( 50% - 1rem );
    }
    &__col-1of3 {
        @include px2-grid__col();
        width: calc( 33.33% - 1rem );
    }
    &__col-2of3 {
        @include px2-grid__col();
        width: calc( 66.66% - 1rem );
    }
    &__col-1of4 {
        @include px2-grid__col();
        width: calc( 25% - 1rem );
    }
    &__col-3of4 {
        @include px2-grid__col();
        width: calc( 75% - 1rem );
    }
    &__col-1of5 {
        @include px2-grid__col();
        width: calc( 20% - 1rem );
    }
    &__col-2of5 {
        @include px2-grid__col();
        width: calc( 40% - 1rem );
    }
    &__col-3of5 {
        @include px2-grid__col();
        width: calc( 60% - 1rem );
    }
    &__col-4of5 {
        @include px2-grid__col();
        width: calc( 80% - 1rem );
    }
    &__col-1of6 {
        @include px2-grid__col();
        width: calc( 16.66% - 1rem );
    }
    &__col-5of6 {
        @include px2-grid__col();
        width: calc( 83.33% - 1rem );
    }

    @media all and (max-width: 768px) {
        // "sm" on Bootstrap 4
        &__col-1of5,
        &__col-2of5,
        &__col-3of5,
        &__col-4of5,
        &__col-1of6,
        &__col-5of6 {
            width: calc( 33.33% - 1rem );
        }
    }

    @media all and (max-width: 660px) {
        &__col-1of4,
        &__col-3of4,
        &__col-1of5,
        &__col-2of5,
        &__col-3of5,
        &__col-4of5,
        &__col-1of6,
        &__col-5of6 {
            width: calc( 50% - 1rem );
        }
    }

    @media all and (max-width: 544px) {
        // "xs" on Bootstrap 4
        &__col-1of3,
        &__col-2of3,
        &__col-1of4,
        &__col-3of4,
        &__col-1of5,
        &__col-2of5,
        &__col-3of5,
        &__col-4of5,
        &__col-1of6,
        &__col-5of6 {
            width: calc( 50% - 1rem );
        }
    }

    @media all and (max-width: 440px) {
        &__col-1of2,
        &__col-1of3,
        &__col-2of3,
        &__col-1of4,
        &__col-3of4,
        &__col-1of5,
        &__col-2of5,
        &__col-3of5,
        &__col-4of5,
        &__col-1of6,
        &__col-5of6 {
            width: calc( 100% - 1rem );
        }
    }


